.con-search{
  margin-bottom: 14px;
  @media screen and (min-width: 768px) {
    margin-bottom: 0px;
  }
}

.text-search{
  color: rgba(5, 5, 5, 0.50);
  font-size: 14px;
  font-family: Inter;
  font-weight: 500;
  line-height: 18px;
  margin-bottom: 8px;
}

.con-inp{   
  position: relative;
}

.inp-search {
  max-width: 335px;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 48px;
  padding-right: 122px;
  border-radius: 15px;
  border: 1px solid rgba(5, 5, 5, 0.20);
  @media screen and (min-width: 768px) {
    max-width: 278px;
  }
}


.option-select{
  border-radius: 14px;
  background: #FFF;
  box-shadow: 0px 4px 36px 0px rgba(0, 0, 0, 0.02);
}

.btn-search{
  outline: 0;
  border: 0;
  background-color: transparent;
  padding: 0;
  width: 18px;
  height: 18px;
  position: absolute;
  top: 15;
  left: 18;
}

.list-item{
  display: flex;
  flex-wrap: wrap;
  gap: 14px;
}

.item{
  padding: 14px 18px;
  border-radius: 15px;
  border: 1px solid rgba(5, 5, 5, 0.20);
}


.time{
  margin-right: 14px;
  @media screen and (min-width: 768px) {
    margin-right: 0;
  }
}

.area{
  width: 160px;
}

.ingredients{
  margin-top: 14px;
  // width: 168px;
}


.con-select{ 
  max-width: 335px;
  display: flex;
  flex-wrap: wrap;  
}

@media screen and (min-width: 768px){
  .con-select{
    max-width: 496px; 
    gap: 16px;
  }
  .list-item{
    display: flex;
  }
  .inp-search{
    width: 278px;
  }
 
  .ingredients{
    margin-top: auto;
  }
}

@media screen and (min-width: 1280px){
  .con-select{
    max-width: 780px;
  }
  .ingredients{
    margin-top: 0;
  }
}


