.custom-select {
  position: relative;
  display: inline-block;
}

.search-arrow{
  position: absolute;
  top: 15px;
  right: 18px;
  bottom: 0;
  display: flex;
  align-items: center;
  pointer-events: none;
  width: 20px;
  height: 20px;
  fill: rgba(5, 5, 5, 0.5);
}

.custom-select .selected-option {
  display: flex;
  align-items: center;
  max-width: 100%;
  width: 160px;
  border: 1px solid #ccc;
  cursor: pointer;
  @media screen and (min-width: 768px) {
    width: 125px;
  }
}

@media screen and (min-width: 768px) {
  .custom-select .selected-area{
    width: 141px;
  }
}

.custom-select .selected-option span {
  margin-right: 5px;
}
  
.custom-select .options-list {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  

  list-style: none;
  background-color: #fff;
  border: 1px solid #ccc;
  border-top: none;
  display: none;
}

.custom-select .options-list {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  z-index: 9;
  max-height: 176px; /* Определите максимальную высоту списка */
  overflow-y: auto; /* Включите вертикальную прокрутку */
  list-style: none;
  background-color: rgba(255, 255, 255, 1);
  border: 1px solid #ccc;
  border-top: none;
  display: none;
}

.custom-select .options-list .option {
  cursor: pointer;
}

.item-ingridienst{
  width: 188px;
  max-width: 100%;
  border-radius: 15px;
  border: 1px solid rgba(5, 5, 5, 0.20);
  padding: 14px 18px;
  cursor: pointer;
  border: 1px solid #ccc;
  align-items: center;
  display: flex;
}

.custom-select.open .options-list {
  display: block;
}
  
.options-list{
  padding-top: 14px;
  padding-left: 18px;
  padding-bottom: 14px;
  border-radius: 14px;
  background-color: rgba(255, 255, 255, 1);
  box-shadow: 0px 4px 36px 0px rgba(0, 0, 0, 0.02);
}

.selected-option{
  padding: 14px 18px;
  border-radius: 15px;
  border: 1px solid rgba(5, 5, 5, 0.20);
}

.elem-prev{
  color: rgba(5, 5, 5, 0.50);
  font-size: 14px;
  font-family: Inter;
  font-weight: 500;
  line-height: 18px;
}

.option{
  color: rgba(5, 5, 5, 0.30);
  font-size: 14px;
  font-family: Inter;
  font-weight: 500;
  line-height: 18px;
}

.option:not(:last-child){
  margin-bottom: 8px;
}
